import { useEffect, useState } from "react";
import { bitcoinService } from "../../services/bitcoinService.js";
import "./HomePage.scss";
import { MoveList } from "../../cmps/MoveList/MoveList.jsx";
import { useDispatch, useSelector } from "react-redux";
import { setUser } from "../../store/actions/userActions.js";
import HomePageImg from "../../assets/imgs/home.jpg";
export const HomePage = (props) => {
  const [bitcoinRate, setBitcoinRate] = useState(0);
  const loggedUser = useSelector((state) => state.userReducer.user);

  const dispatch = useDispatch();

  useEffect(() => {
    const loadBitcoinRate = async () => {
      const fetchRate = await bitcoinService.getRate();
      setBitcoinRate(fetchRate);
    };
    loadBitcoinRate();
    dispatch(setUser());
  }, []);

  if (!loggedUser)
    return (
      <div>
        <img src={HomePageImg} alt="" />
      </div>
    );

  return (
    <div className="home-page">
      <h1>Hello {loggedUser.name}</h1>
      <h3>You have {loggedUser.coins} coins</h3>
      <h3>Bitcoin rate {bitcoinRate} per 1$</h3>
      {/* <MoveList moves={loggedUser.moves} /> */}
    </div>
  );
};
